import $ from 'jquery';
import daterangepicker from 'daterangepicker';

window.datepickerChange = function (el){
    // console.log(el);
};

document.addEventListener('DOMContentLoaded', () => {
    window.datepickerInit()
})

window.datepickerInit = function (){
    let datepickers = $('.jsInputDate');

    datepickers.each(function(e){
    
        $(this).daterangepicker({
            locale: {
                format: "DD/MM/YYYY",
                separator: " - ",
                applyLabel: "Применить",
                cancelLabel: "Отмена",
                fromLabel: "От",
                toLabel: "До",
                customRangeLabel: "Custom",
                weekLabel: "W",
                daysOfWeek: [
                    "Вс",
                    "Пн",
                    "Вт",
                    "Ср",
                    "Чт",
                    "Пт",
                    "Сб"
                ],
                monthNames: [
                    "Январь",
                    "Февраль",
                    "Март",
                    "Апрель",
                    "Май",
                    "Июнь",
                    "Июль",
                    "Август",
                    "Сентябрь",
                    "Октябрь",
                    "Ноябрь",
                    "Декабрь"
                ],
                firstDay: 1
            },
            autoUpdateInput: false,
            linkedCalendars: false,
            buttonClasses: "button button--sm",
            applyButtonClasses: "",
            cancelClass: "button--light",
            drops: "auto",
            opens: "center"
        });
    
        $(this).on('apply.daterangepicker', function(ev, picker) {
            $(this).val(picker.startDate.format('DD/MM/YYYY') + ' - ' + picker.endDate.format('DD/MM/YYYY'));
            
            if($(this).closest('.filter')){
                $(this).closest('.filter').find('input[name="date_from"').val(picker.startDate.format('DD.MM.YYYY'))
                $(this).closest('.filter').find('input[name="date_to"').val(picker.endDate.format('DD.MM.YYYY'))
            }
    
            window.datepickerChange($(this)[0])
        });
    
        $(this).on('cancel.daterangepicker', function(ev, picker) {
            $(this).val('');
                    
            if($(this).closest('.filter')){
                $(this).closest('.filter').find('input[name="date_from"').val('')
                $(this).closest('.filter').find('input[name="date_to"').val('')
            }
    
            window.datepickerChange($(this)[0])
        });
    })
    
    let datepickersSingle = $('.jsInputDateSingle');
    
    datepickersSingle.each(function(){
        $(this).daterangepicker({
            locale: {
                format: "DD/MM/YYYY",
                separator: " - ",
                applyLabel: "Применить",
                cancelLabel: "Отмена",
                fromLabel: "От",
                toLabel: "До",
                customRangeLabel: "Custom",
                weekLabel: "W",
                daysOfWeek: [
                    "Вс",
                    "Пн",
                    "Вт",
                    "Ср",
                    "Чт",
                    "Пт",
                    "Сб"
                ],
                monthNames: [
                    "Январь",
                    "Февраль",
                    "Март",
                    "Апрель",
                    "Май",
                    "Июнь",
                    "Июль",
                    "Август",
                    "Сентябрь",
                    "Октябрь",
                    "Ноябрь",
                    "Декабрь"
                ],
                firstDay: 1
            },
            "singleDatePicker": true,
            autoUpdateInput: false,
            linkedCalendars: false,
            buttonClasses: "button button--sm",
            applyButtonClasses: "",
            cancelClass: "button--light",
            drops: "auto",
            opens: "center"
        });
    
        $(this).on('apply.daterangepicker', function(ev, picker) {
            $(this).val(picker.startDate.format('DD.MM.YYYY'));
            window.inputStatusAdd($(this)[0], 'filled')
    
            if($(this).closest('.filter')){
                $(this).closest('.filter').find('input[name="date_from"').val(picker.startDate.format('DD.MM.YYYY'))
            }
    
            window.datepickerChange($(this)[0])
        });
    
        $(this).on('cancel.daterangepicker', function(ev, picker) {
            $(this).val('');
            window.inputStatusRemove($(this)[0], 'filled')
                            
            if($(this).closest('.filter')){
                $(this).closest('.filter').find('input[name="date_from"').val('')
            }

            
            window.datepickerChange($(this)[0])
        });
    })
}

