document.addEventListener('DOMContentLoaded', function () {
    const jsAccordions = document.querySelectorAll('.jsAccordion');


    for (let index = 0; index < jsAccordions.length; index++) {
        let item = jsAccordions[index];
        let body = item.querySelector('.jsAccordionBody');
        let content = item.querySelector('.jsAccordionContent');
        let button = item.querySelector('.jsAccordionTrigger');

        if(button) {
            check()

            button.addEventListener('click', () => {
                item.classList.toggle('is-hide');

                check()
            })
        }

        function check(){
            if(!item.classList.contains('is-hide')){
                body.style.maxHeight = content.offsetHeight + 'px';
            } else {
                body.style.maxHeight = content.offsetHeight + 'px';
            
                setTimeout(() => {
                    body.style.maxHeight = '0px';
                }, 1);
            }
        }
    }
})

