import moment from 'moment';

window.moment = moment();

window.calendarEvents = {
    item: document.querySelector('.jsCalendarEvents'),
    inputs: document.querySelectorAll('.jsFilterDate'),
    list: [],
}

if (window.calendarEvents.item) {
    const calendarItem = window.calendarEvents.item;
    const calendarInner = calendarItem.querySelector('.swiper-wrapper', 'slider__wrapper');
    let localDate = moment().clone();
    let thisMonth = localDate.clone().format('M');
    localDate = localDate.startOf('year');

    window.calendarEvents.init = function (){
        for (let i = 0; i < 12; i++) {
            localDate = newMonth(localDate);
        }

        window.calendarEvents.slider.update()
        window.calendarEvents.slider.slideTo(thisMonth - 1, 0)
        window.calendarEvents.inputs= document.querySelectorAll('.jsFilterMonth');
    }

    document.addEventListener('DOMContentLoaded', () => {
        window.calendarEvents.init()

        for (let index = 0; index < window.calendarEvents.inputs.length; index++) {
            const item = window.calendarEvents.inputs[index];

            if(index== (thisMonth - 1)){
                item.classList.add('is-active')
                click ()
            }
            
            item.addEventListener('click', ()=>{
                click ()
            })

            function click () {
                let value = item.getAttribute('data-calendar-date');
                clearAll()
                
                item.classList.add('is-active')
                
                callback()

                function callback(){
                    if(item.closest('.filter')){
                        item.closest('.filter').querySelector('input[name="date_from"').value = value
                    }
    
                    window.datepickerChange(item)
                }

                function clearAll(){
                    for (let index = 0; index < window.calendarEvents.inputs.length; index++) {
                        const item = window.calendarEvents.inputs[index];
                        item.classList.remove('is-active')
                    }    
                }
            }
        }
    })
    
    function newMonth(localDate){
        const tempDate = localDate.clone();
        const monthStart = localDate.clone();
        const monthEnd = tempDate.clone().endOf('month');
        const monthName = tempDate.format('MMMM');

        // Создание и наполнение
        let slide = document.createElement('div');
        let month = document.createElement('div');
        let name = translateMonth(monthName);

        slide.classList.add('swiper-slide', 'slider__slide');
        month.classList.add('calendar-events__item', 'jsFilterMonth');
        month.innerHTML = `<span>` + name + `</span>`;
        month.setAttribute('data-calendar-date', monthStart.format('YYYY-MM-DD'));

        calendarInner.append(slide)
        slide.append(month)
        // 

        window.calendarEvents.list.push(name)
        return monthEnd.add(1, 'day');
    }

    function translateMonth(monthName){
        let name;
        
        switch(monthName) {
            case 'January':
                name = 'Январь';
                break;
                
            case 'February':
                name = 'Февраль';
                break;
                
            case 'March':
                name = 'Март';
                break;
                
            case 'April':
                name = 'Апрель';
                break;
                    
            case 'May':
                name = 'Май';
                break;
                    
            case 'June':
                name = 'Июнь';
                break;
                    
            case 'July':
                name = 'Июль';
                break;
            
            case 'August':
                name = 'Август';
                break;
            
            case 'September':
                name = 'Сентябрь';
                break;
            
            case 'October':
                name = 'Октябрь';
                break;
    
            case 'November':
                name = 'Ноябрь';
                break;

            case 'December':
                name = 'Декабрь';
                break;
                
            default:
                break;
        }

        return name;
    }
}