import moment from 'moment';

window.moment = moment();

moment.updateLocale('en', {
    week : {
        dow : 1
    }
});

window.calendar = {
    item: document.querySelector('.jsCalendar'),
    inputs: document.querySelectorAll('.jsFilterDate'),
    list: [],
}

if (window.calendar.item) {
    const calendarItem = window.calendar.item;
    const calendarInner = calendarItem.querySelector('.swiper-wrapper', 'slider__wrapper');
    let localDate = moment().clone();

    document.addEventListener('DOMContentLoaded', () => {
        init()

        for (let index = 0; index < window.calendar.inputs.length; index++) {
            const item = window.calendar.inputs[index];
            
            item.addEventListener('click', (e)=>{
                let all = window.calendar.item.querySelectorAll('.jsFilterDate');

                if(window.calendar.item.querySelectorAll('.jsFilterDate.is-active').length >= 2){
                    for (let index = 0; index < all.length; index++) {
                        const item = all[index];
                        item.classList.remove('is-range')
                        item.classList.remove('is-active')
                    }
                } 

                item.classList.add('is-active')

                run()

                function run(){
                    let allChecked = window.calendar.item.querySelectorAll('.jsFilterDate.is-active');
                    let array = [],
                        minTemp,
                        maxTemp,
                        minItem,
                        maxItem,
                        minIndex,
                        maxIndex,
                        min = '',
                        max = '';

                    findMinMax()
                    findIndex()
                    callback ()

                    function findMinMax(){
                        for (let index = 0; index < allChecked.length; index++) {
                            const item = allChecked[index];
                            let value = Date.parse(item.getAttribute('data-calendar-date'));
                            array.push(value);
                        }
    
                        minTemp = Math.min.apply(null, array);
                        maxTemp = Math.max.apply(null, array);
    
                        for (let index = 0; index < allChecked.length; index++) {
                            const item = allChecked[index];
    
                            let value = item.getAttribute('data-calendar-date');
    
                            // minTemp == Date.parse(value) ? (min = value, minItem = item) : maxTemp == Date.parse(value) ? (max = value, maxItem = item): '';
    
                            if(minTemp == Date.parse(value)){
                                min = value;
                                minItem = item;
    
                            } else if(maxTemp == Date.parse(value)){
                                max = value;
                                maxItem = item;
                            }
                        }
                    }
      
                    function findIndex(){

                        for (let index = 0; index < all.length; index++) {
                            const item = all[index];

                            if(item == minItem){
                                minIndex = index;
                            } else if(item == maxItem) {
                                maxIndex = index;
                            }
                        }

                        if(maxIndex != undefined){
                            let tempAll = Array.prototype.slice.call(all, minIndex, maxIndex)

                            for (let index = 0; index < tempAll.length; index++) {
                                const item = tempAll[index];
                                item.classList.add('is-range');
                            }
                        }
                    }

                    function callback () {
                        let filterWrapper = item.closest('.filter');

                        if(filterWrapper){
                            filterWrapper.querySelector('input[name="date_from"').value = min
                            filterWrapper.querySelector('input[name="date_to"').value = max
                        }
    
                        if(min != '' && max != '') {
                            window.datepickerChange(filterWrapper.querySelector('input[name="date_from"'))
                        }
                    }
                }
            })
        }
    })

    function init(){
        for (let i = 0; i < 13; i++) {
            localDate = newMonth(localDate, i);
        }
        window.calendar.slider.update()
        window.calendar.inputs= document.querySelectorAll('.jsFilterDate');
    }
    
    function newMonth(localDate, index){
        const tempDate = localDate.clone();
        let monthStart = index == 0 ? localDate.clone() : tempDate.clone().startOf('month');
        let monthEnd = index == 12 ? tempDate.clone().subtract(1, 'day') : tempDate.clone().endOf('month');
        let monthName = tempDate.format('MMMM');
        let monthList = [];
        let day = monthStart.clone();  
        let monthInner ;

        // Создание
        let slide = document.createElement('div');
        let month = document.createElement('div');
        let days = document.createElement('div');
        let name = translateMonth(monthName);
        slide.classList.add('swiper-slide', 'slider__slide');
        month.classList.add('calendar__month');
        days.classList.add('calendar__days');
        month.innerHTML = `<span>` + name + `</span>`;
        month.append(days)

        calendarInner.append(slide)
        slide.append(month)

        monthInner = days;
        // 

        // Наполнение
        while (!day.isAfter(monthEnd)){
            let dayDate = day.clone();
            monthList.push(day.clone());
            createDay(monthInner, dayDate)

            day.add(1, 'day');
        }
    
        window.calendar.list.push(monthList)
        // 

        return tempDate.clone().add(1, 'month');
    }
    
    function createDay(parent, date) {
        let thisDate = date;
        let name = translateDay(thisDate.format('dd'));
        
        let number = thisDate.format('DD');
        let value = thisDate.format('YYYY-MM-DD');
    
        let dayPattern = `
                <strong>` + number + `</strong>
                <span>` + name + `</span>
        `;
    
        let element = document.createElement('div');
        element.classList.add('calendar__day', 'jsFilterDate');
        element.setAttribute('data-calendar-date', value);
        
        if (name == 'Сб' || name == 'Вс') {
            element.classList.add('calendar__day--weekend')
        }
    
        element.innerHTML = dayPattern;
        parent.append(element)
    }

    function translateMonth(monthName){
        let name;
        
        switch(monthName) {
            case 'January':
                name = 'Январь';
                break;
                
            case 'February':
                name = 'Февраль';
                break;
                
            case 'March':
                name = 'Март';
                break;
                
            case 'April':
                name = 'Апрель';
                break;
                    
            case 'May':
                name = 'Май';
                break;
                    
            case 'June':
                name = 'Июнь';
                break;
                    
            case 'July':
                name = 'Июль';
                break;
            
            case 'August':
                name = 'Август';
                break;
            
            case 'September':
                name = 'Сентябрь';
                break;
            
            case 'October':
                name = 'Октябрь';
                break;
    
            case 'November':
                name = 'Ноябрь';
                break;

            case 'December':
                name = 'Декабрь';
                break;
                
            default:
                break;
        }

        return name;
    }

    function translateDay(dayName){
        let name;
        
        switch(dayName) {
            case 'Mo':
                name = 'Пн';
                break;
                
            case 'Tu':
                name = 'Вт';
                break;
                
            case 'We':
                name = 'Ср';
                break;
                
            case 'Th':
                name = 'Чт';
                break;
                    
            case 'Fr':
                name = 'Пт';
                break;
                    
            case 'Sa':
                name = 'Сб';
                break;
                    
            case 'Su':
                name = 'Вс';
                break;
       
            default:
                break;
        }

        return name;
    }
}