export default (settings = {}) => {
  const { fromNode, toNode } = settings;

  if (!fromNode || !toNode) {
    throw new Error('timeComponent');
  }

  const containerNode = fromNode.closest('.input__field-group');

  const state = {
    value: {
      from: '',
      to: '',
    },
  };

  const normalizeValue = (value) => {
    if (typeof value === 'string') {
      return value ? JSON.parse(value) : { from: '', to: '' };
    }

    return value;
  };

  const setValue = (newValue = {}) => {
    const oldValue = state.value;

    state.value = {
      ...oldValue,
      ...normalizeValue(newValue),
    };
  };

  const getValue = (key = '') => {
    const { value } = state;
    const copyValue = { ...value };

    if (!key) {
      return copyValue;
    }

    if (Object.hasOwn(copyValue)) {
      return value[key];
    }

    return '';
  };

  const validate = () => {
    const { from, to } = state.value;

    if (!from || !to) {
      return false;
    }

    return true;
  };

  const toString = () => JSON.stringify(state.value);

  const renderValue = () => {
    const { from, to } = state.value;
    fromNode.value = from;
    toNode.value = to;
  };

  const renderValidity = (validityState = '') => {
    if (validityState === 'invalid') {
      containerNode.classList.add('_invalid');
      return;
    }

    containerNode.classList.remove('_invalid');
  };

  return {
    fromNode,
    toNode,
    validate,
    renderValidity,
    toString,
    getValue,
    setValue,
    update: (newValue) => {
      setValue(newValue);
      renderValue();
      renderValidity('idle');
      return getValue();
    },
    clear: () => {
      setValue('');
      renderValue();
      renderValidity('valid');
    },
  };
};
