import moment, { updateLocale } from 'moment';

window.calendarTable = {
    item: document.querySelector('.jsCalendarTable'),
}

if (window.calendarTable.item) {
    const calendarItem = window.calendarTable.item;
    const calendarInner = calendarItem.querySelector('.swiper-wrapper', 'slider__wrapper');
    const monthCount = calendarItem.getAttribute('data-table-months') ? calendarItem.getAttribute('data-table-months') : 3;

    let localDate = moment().clone();
    let ThisDateFormat = moment().clone().format('YYYY-MM-DD');

    document.addEventListener('DOMContentLoaded', () => {
        init()
    })

    function init(){
        for (let i = 0; i < monthCount; i++) {
            localDate = newMonth(localDate, i);
        }
        window.calendarTable.slider.update()
    }
    
    function newMonth(localDate){
        const tempDate = localDate.clone();
        let tableStart = tempDate.clone().startOf('month').startOf('week');
        let tableEnd = tempDate.clone().endOf('month').endOf('week');
        let monthName = tempDate.format('MMMM');
        let day = tableStart.clone();  
        let tbody ;
        let mainPattern = `
            <div class="calendar-table__main">
                <div class="calendar-table__thead">
                    <div class="calendar-table__thead-col">Пон</div>
                    <div class="calendar-table__thead-col">Вто</div>
                    <div class="calendar-table__thead-col">Сре</div>
                    <div class="calendar-table__thead-col">Чет</div>
                    <div class="calendar-table__thead-col">Пят</div>
                    <div class="calendar-table__thead-col">Суб</div>
                    <div class="calendar-table__thead-col">Вос</div>
                </div>
                <div class="calendar-table__tbody"></div>
            </div>
        `;

        // Создание
        let slide = document.createElement('div');
        slide.classList.add('swiper-slide', 'slider__slide');
        slide.innerHTML = mainPattern;
        slide.setAttribute('data-calendar-date', [translateMonth(monthName)+ '; ' +tempDate.format('YYYY')])
        tbody = slide.querySelector('.calendar-table__tbody');

        calendarInner.append(slide)

        // Наполнение
        while (!day.isAfter(tableEnd)){
            let dayDate = day.clone();
            createDay(tbody, dayDate, monthName)

            day.add(1, 'day');
        }
    

        return tempDate.clone().add(1, 'month');
    }

    function createDay(parent, date, month) {
        let thisDate = date;
        let thisMonth = thisDate.format('MMMM');
        let number = thisDate.format('DD');
        let value = thisDate.format('YYYY-MM-DD');
        let dayPattern = `
            <div class="calendar-table__day-number">
                <span>` + number + `</span>
            </div>
            <div class="calendar-table__day-list">

            </div>
            <div class="calendar-table__day-actions">
                <button class="button calendar-table__day-add" type="button"data-bs-toggle="modal" data-bs-target="#eventAdd">
                    <svg class="icon button__icon">
                        <use xlink:href="#plus"></use>
                    </svg>
                </button>
                <button class="link calendar-table__day-link" type="button"data-bs-toggle="modal" data-bs-target="#eventList">
                    <span>Все</span>
                </button>
            </div>
        `;

        // Создание
        let element = document.createElement('div');
        element.classList.add('calendar-table__day');
        element.setAttribute('data-calendar-date', value);
        element.innerHTML = dayPattern;
        
        if (thisMonth != month || thisDate.isBefore(ThisDateFormat)) {
            element.setAttribute('disabled', true)
        }

        if (thisDate.format('YYYY-MM-DD') == ThisDateFormat){
            element.classList.add('calendar-table__day--today')
        }
    
        parent.append(element)
    }

    function translateMonth(monthName){
        let name;
        
        switch(monthName) {
            case 'January':
                name = 'Январь';
                break;
                
            case 'February':
                name = 'Февраль';
                break;
                
            case 'March':
                name = 'Март';
                break;
                
            case 'April':
                name = 'Апрель';
                break;
                    
            case 'May':
                name = 'Май';
                break;
                    
            case 'June':
                name = 'Июнь';
                break;
                    
            case 'July':
                name = 'Июль';
                break;
            
            case 'August':
                name = 'Август';
                break;
            
            case 'September':
                name = 'Сентябрь';
                break;
            
            case 'October':
                name = 'Октябрь';
                break;
    
            case 'November':
                name = 'Ноябрь';
                break;

            case 'December':
                name = 'Декабрь';
                break;
                
            default:
                break;
        }

        return name;
    }
}

window.setCalendarEvents = (link) => {
    let request = new XMLHttpRequest();
    request.open('GET', link);
    request.responseType = 'json';
    request.send();

    request.onload = function() {
        var daysList = request.response;

        addEvents(daysList)
        window.calendarTable.slider.update()
    } 

    function addEvents (list) {
        let block = document.querySelector('.jsCalendarTable');

        for (let index = 0; index < list.length; index++) {
            const day = list[index];
            
            if(block.querySelector(`[data-calendar-date="${day.date}"]`)) {
                addDate(day)
            }
        }
    }

    function addDate (day){
        let date = day.date;
        let list = day.list;
        let block = document.querySelector('.jsCalendarTable');
        let days = block.querySelectorAll(`[data-calendar-date="${date}"]`)

        days.forEach(day => {
            let dayList = day.querySelector('.calendar-table__day-list');

            list.forEach((event, index) => {
                let eventElement = createEvent(event.name, event.time, index);
                dayList.append(eventElement);
            })

            checkLength(day)
        })

        function checkLength(day){
            let link = day.querySelector('.calendar-table__day-link');
            let events = day.querySelectorAll('.calendar-table__day-event');

            if (events.length > 2) {
                link.classList.add('is-visible')
            }
        }
    }

    function createEvent (name, time, index) {
        let element = document.createElement('div');
        let content = `
            <h4>${name}</h4>
            <span>${time}</span>
        `;
        element.classList.add('calendar-table__day-event');
        element.setAttribute('style', 'animation-delay:'+ (index * 0.1) +'s')
        element.innerHTML = content;

        return element;
    }
}