/* eslint-disable no-plusplus */
/* eslint-disable no-use-before-define */
import timeComponent from './lib/timeComponent.js';
import dayComponent from './lib/dayComponent.js';

const scheduleComponent = (componentNode) => {
  const state = {
    focusedDayIndex: 'idle',
  };

  const setFocusedDayIndex = (newValue) => {
    state.focusedDayIndex = newValue;
  };

  const unsetFocusedDayIndex = () => {
    state.focusedDayIndex = 'idle';
  };

  const hasFocusedDay = () => state.focusedDayIndex !== 'idle';

  const validateCurrentDay = () => {
    if (!hasFocusedDay()) {
      return true;
    }

    return time.validate();
  };

  const updateCurrentDayTime = () => {
    if (!hasFocusedDay()) {
      setFocusedDayIndex(0);
    }

    const { focusedDayIndex } = state;
    const currentTime = time.toString(time.getValue());
    const currentDay = days[focusedDayIndex];

    currentDay.updateTime(currentTime);
  };

  const renderUnfocusedDays = () => {
    if (!hasFocusedDay()) {
      return;
    }

    const { focusedDayIndex } = state;
    const activeDays = days.filter((day) => day.getActivity() === 'active');

    activeDays.forEach(({ node }, index) => {
      if (index !== focusedDayIndex) {
        node.classList.add('is-unfocused');
        return;
      }

      node.classList.remove('is-unfocused');
    });
  };

  const block = componentNode;
  const dayNodes = Array.from(block.querySelectorAll('.jsScheduleDay'));
  const inputFrom = block.querySelector('.jsScheduleInputFrom');
  const inputTo = block.querySelector('.jsScheduleInputTo');

  const time = timeComponent({
    fromNode: inputFrom,
    toNode: inputTo,
  });

  const days = dayNodes.map((dayNode) => dayComponent({ dayNode }));
  const [firstDay] = days;

  time.update(firstDay.getTime());

  days.forEach((day, index) => {
    const { node } = day;

    node.addEventListener('click', () => {
      const focusedDayValidity = validateCurrentDay() ? 'valid' : 'invalid';
      const currentTimeString = time.toString(time.getValue());
      const dayTime = day.getTime();
      const dayActivity = day.getActivity();
      const dayFocusing = index === state.focusedDayIndex ? 'focused' : 'unfocused';

      if (dayActivity === 'active' && dayFocusing === 'focused') {
        unsetFocusedDayIndex();
        renderUnfocusedDays();
        day.updateActivity('inactive');
        day.updateTime('');
        time.clear();
        return;
      }

      if (dayFocusing === 'unfocused' && focusedDayValidity === 'invalid') {
        time.renderValidity(focusedDayValidity);
        return;
      }

      setFocusedDayIndex(index);
      renderUnfocusedDays();
      day.updateActivity('active');

      if (dayActivity === 'active') {
        time.update(dayTime);
        return;
      }

      day.updateTime(currentTimeString);
    });
  });

  time.fromNode.addEventListener('change', (event) => {
    time.setValue({ from: event.target.value });
    updateCurrentDayTime();
  });

  time.toNode.addEventListener('change', (event) => {
    time.setValue({ to: event.target.value });
    updateCurrentDayTime();
  });
};

const init = (selector = '.jsSchedulePicker') => {
  const componentNodes = Array.from(document.querySelectorAll(selector));

  componentNodes.forEach(scheduleComponent);
};

document.addEventListener('DOMContentLoaded', () => {
  init();
});

window.jsSchedulePicker = init;
